@import "../../../utilities/accessibility/a11y";

.action-form {
  > .panel {
    display: inline-block;
    vertical-align: top;
    border: none;
  }
  > .panel > .title {
    @extend ._sr-only;
  }
  > .panel.-results {
    width: (1 / 3) * 100%;
  }
}
