.theme-base {
  > .view {
    color: inherit;
    background-color: inherit;
    font-family: inherit;
    font-size: inherit;
    line-height: inherit;
    word-spacing: inherit;
    letter-spacing: inherit;
  }
}

@media only print {
  .theme-base {
    color: #000 !important;
    background-color: transparent !important;
  }
}
