@import "../../../utilities/accessibility/scrollable";

.-modal-open {
  overflow: hidden;
}

.np-modal {
  display: none;
  content: ' ';
  background-color: rgba(0,0,0,0.75);
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 6;
  > .content {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    @extend ._scrollable;
  }
  &.-open {
    display: block;
  }
}
