@import "../../../config/config";
@import "../../../utilities/functions/deep-get";

.np-search {
  position: fixed;
  top: 0;
  right: 0;
  z-index: 2;
  > .content {
    position: fixed;
    top: 0;
    right: 0;
    width: 33.333333333333%;
    height: 100%;
    padding-right: 16.666666666667%;
  }
  > .content > .container {
    height: 100%;
  }
  .-no-script & {
    position: static;
    margin-left: auto;
    > .content {
      position: static;
      width: auto;
    }
  }
}

@media (max-width: deep-get($config, 'breakpoints.xxl')) {
  .np-search {
    > .content {
      padding-right: 150px;
      width: 33.333333333333%;
    }
  }
}

@media (max-width: deep-get($config, 'breakpoints.xl')) {
  .np-search {
    > .content {
      padding-right: 0;
    }
  }
}

@media (max-width: deep-get($config, 'breakpoints.md')) {
  .np-search {
    > .content {
      padding-right: 0;
    }
    .-no-script & {
      position: static;
      > .content {
        position: static;
      }
    }
  }
}
