@import "../../../utilities/accessibility/scrollable";

.nav-sidebar {
  width: 100%;
  height: 100%;
  position: relative;

  &.-with-header > .header {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
  }

  > .content {
    height: 100%;
    overflow-y: auto;
    @extend ._scrollable;
  }

  .-no-script {
    position: static;
    &.-with-header > .header {
      position: static;
    }

    > .content {
      height: auto;
    }
  }
}
