.tag-list {
  display: inline-block;
  vertical-align: middle;
  > .title {
    display: inline-block;
    margin-top: 0;
    margin-bottom: 0;
    float: left;
  }
  > .tags {
    display: block;
    padding-left: 0;
    margin-top: 0;
    margin-bottom: 0;
  }
}
