 */

.omni-search {
  height: 100%;
  position: relative;
  > .input {
    height: 100%;
  }
  > .input > .control {
    height: 100%;
    padding: 0;
    background-color: transparent;
    border: none;
    font-family: inherit;
    color: inherit;
  }
  > .input > .control:focus {
    outline: none;
  }
  > .submit {
    height: 100%;
    position: absolute;
    top: 0;
    right: 0;
    background-color: transparent;
    border: none;
    font-family: inherit;
    color: inherit;
    padding: 0;
  }
}
