@import "../../../utilities/accessibility/a11y";
@import "../../../utilities/accessibility/clearfix";

.single-article {
  > .header {
    @extend ._clearfix;
  }
  > .header > .info > .primary {
    float: left;
  }
  > .header > .actions {
    float: right;
    padding: 0;
    margin: 0;
  }
  > .header > .actions > .action {
    display: inline-block;
  }
  > .section.-titleless > .title {
    @extend ._sr-only;
  }

  // TODO refactor
  &.-current .article-info > .title-container > .titles {
    position: fixed;
    z-index: 1;
    top: 0;
    left: 0;
    width: 100%;
  }
}
