.np-splash {
  > .content {
    display: table-cell;
    vertical-align: middle;
    text-align: center;
  }
  > .content > .brand {
    white-space: nowrap;
    display: inline-block;
    margin-top: 0;
    margin-bottom: 0;
    font-size: 3em;
  }
  > .content > .tagline {
    margin-bottom: 0;
    font-family: "Open Sans", "M+ 2p", sans-serif;
  }
}
