@import "../../../utilities/accessibility/a11y";

.labelled-control {
  display: block;
  > .label {
    display: block;
  }
  > .control {
    display: block;
    width: 100%;
    font-weight: inherit;
  }
  &.-hidden-label > .label {
    @extend ._sr-only;
  }
}
