.icon-text {
  display: inline-table;
  position: relative;
  text-align: center;
  vertical-align: middle;
  line-height: 1;
  > .text {
    display: inline-block;
    width: 100%;
    left: 0;
    position: absolute;
    text-transform: uppercase;
    font-weight: bolder;
  }
  > .text:first-child {
    top: 0;
  }
  > .icon-wrapper {
    display: table-cell;
    width: 100%;
    top: 0;
    left: 0;
    vertical-align: middle;
  }
  > .icon-wrapper:first-child {
    vertical-align: top;
  }
  > .icon-wrapper + .text {
    bottom: 0;
  }
}
