@import "../../../utilities/accessibility/scrollable";

.album-playlist {
  @extend ._scrollable;
  overflow-x: auto;
  > .playlist {
    width: 100%;
    min-width: 500px;
  }
  > .playlist > .header > tr > th {
    font-weight: normal;
    text-align: left;
  }
}
