.article-info {
  > .title-container {
    > .titles > .generic-container {
      white-space: nowrap;
      overflow: hidden;
      > .title {
        margin-top: 0;
        margin-bottom: 0;
        overflow: hidden;
        text-overflow: ellipsis;
      }
      > .subtitle {
        margin-top: 0;
        margin-bottom: 0;
        overflow: hidden;
        text-overflow: ellipsis;
      }
    }
  }
}
