// TODO add np-view for children, set position: absolute for fixed containers, specify -root for fixed, non-root for absolute

@import "../../../config/config";
@import "../../../utilities/functions/deep-get";

.np-view {
  > .splash {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: table;
  }

  > .toggle {
    position: fixed;
    top: 0;
    left: 0;
    padding-left: (1 / 6) * 100%;
    width: (1 / 3) * 100%;
    z-index: 5;
  }

  > .centerpiece {
    padding-right: (1 / 3) * 100%;
    padding-left: (1 / 3) * 100%;
  }

  > .actions {
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    z-index: 1;
  }

  .-no-script & {
    > .toggle {
      position: static;
      display: none;
    }

    > .centerpiece {
      padding-right: 150px;
      padding-left: (1 / 3) * 100%;
    }
  }
}

@media (max-width: deep-get($config, 'breakpoints.xxl')) {
  .np-view {
    > .toggle {
      padding-left: 150px;
      width: (1 / 4) * 100%;
    }

    > .search {
      padding-right: 150px;
      width: (1 / 3) * 100%;
    }

    > .centerpiece {
      padding-right: (1 / 4) * 100%;
      padding-left: (1 / 4) * 100%;
    }

    .-sidebar-open & {
      > .centerpiece {
        padding-left: (1 / 3) * 100%;
        padding-right: 150px;
      }

      > .toggle {
        width: (1 / 3) * 100%;
      }
    }

    .-no-script & {
      > .toggle {
        display: none;
      }

      > .centerpiece {
        padding-right: 150px;
        padding-left: (1 / 3) * 100%;
      }
    }
  }
}

@media (max-width: deep-get($config, 'breakpoints.xl')) {
  .np-view {
    > .centerpiece {
      padding-right: (1 / 6) * 100%;
      padding-left: (1 / 6) * 100%;
    }

    .-sidebar-open & {
      > .centerpiece {
        padding-right: 0;
      }

      > .toggle {
        padding-left: 0;
        width: (1 / 3) * 100%;
      }
    }

    > .toggle {
      padding-left: 0;
      width: (1 / 6) * 100%;
    }

    > .search {
      padding-right: 0;
    }
  }
}

@media (max-width: deep-get($config, 'breakpoints.md')) {
  .np-view {
    > .centerpiece {
      padding-left: 0;
      padding-right: 0;
    }

    > .toggle {
      width: auto;
    }

    > .search {
      width: auto;
    }

    .-sidebar-open & {
      overflow: hidden;
      > .toggle {
        width: deep-get($config, 'sizes.sidebar');
      }

      > .centerpiece {
        padding-left: 0;
      }
    }

    .-no-script & {
      overflow: auto;
      > .sidebar {
        width: auto;
        position: static;
      }

      > .toggle {
        position: static;
        display: block;
      }

      > .search {
        display: block;
      }

      > .centerpiece {
        padding-left: 0;
        padding-right: 0;
      }
    }
  }
}
