.clock-icon {
  > .frame {
    display: inline-block;
    position: relative;
    height: 0.8em;
    width: 0.8em;
    border: 0.05em solid;
    border-radius: 50%;
  }

  > .frame > .hands {
    position: absolute;
    top: 50%;
    left: 50%;
    display: block;
    width: 50%;
    height: 50%;
  }

  > .frame > .hands > .hand {
    transform-origin: left;
    position: absolute;
    top: -0.025em;
    left: -0.025em;
    background-color: currentColor;
    height: 0.05em;
    &::before {
      content: ' ';
      display: block;
      position: absolute;
      left: -0.025em;
      top: 0;
      background-color: currentColor;
      height: 0.05em;
      width: 0.05em;
      border-radius: 0.025em;
    }
    &::after {
      content: ' ';
      display: block;
      position: absolute;
      right: -0.025em;
      top: 0;
      background-color: currentColor;
      height: 0.05em;
      width: 0.05em;
      border-radius: 0.025em;
    }
  }

  > .frame > .hands > .hand.-hour {
    width: 50%;
  }

  > .frame > .hands > .hand.-minute {
    width: 80%;
  }
}
