@import "../../../utilities/functions/deep-get";
@import "../../../config/config";

.gallery-collection {
  width: 100%;
  > .items {
    margin-top: 0;
    margin-bottom: 0;
    padding-left: 0;
    width: 100%;
  }
  > .items > .item {
    display: inline-block;
    width: (1 / 8) * 100%;
    vertical-align: top;
    padding: 2px;
  }
}

@media (max-width: deep-get($config, 'breakpoints.lg')) {
  .gallery-collection {
    > .items > .item {
      width: (1 / 6) * 100%;
    }
  }
}

@media (max-width: deep-get($config, 'breakpoints.md')) {
  .gallery-collection {
    > .items > .item {
      width: (1 / 4) * 100%;
    }
  }
}
