.noUi-connect {
  box-shadow: none;
}

.noUi-handle {
  border: none;
}

.noUi-target {
  border: none;
  box-shadow: none;
  cursor: pointer;
}

.noUi-horizontal {
  .noUi-handle {
    left: 0;
    top: 0;
    cursor: inherit;
  }
}

.noUi-handle {
  box-shadow: none;
  border-radius: 50%;

  &::before {
    display: none;
  }

  &::after {
    display: none;
  }
}
