@import "../../../config/config";
@import "../../../utilities/functions/deep-get";
@import "../../../utilities/accessibility/scrollable";

.np-sidebar {
  display: none;
  content: ' ';
  background-color: rgba(0,0,0,0.75);
  position: fixed;
  top: 0;
  left: 0;
  width: 0;
  height: 0;
  z-index: 4;
  > .content {
    @extend ._scrollable;
    position: fixed;
    top: 0;
    left: -33.333333333333%;
    height: 100%;
    z-index: 2;
    width: 33.333333333333%;
    padding-left: 16.666666666667%;
  }
  &.-open, .-no-script & {
    display: block;
    height: auto;
    > .content {
      left: 0;
    }
  }
}

@media (max-width: deep-get($config, 'breakpoints.xxl')) {
  .np-sidebar {
    > .content {
      padding-left: 150px;
    }

    .-no-script & {
      > .content {
        left: 0;
      }
    }
  }
}


@media (max-width: deep-get($config, 'breakpoints.xl')) {
  .np-sidebar {
    > .content {
      padding-left: 0;
    }
  }
}

@media (max-width: deep-get($config, 'breakpoints.md')) {
  .np-sidebar {
    &.-open {
      width: 100%;
      height: 100%;
    }
    > .content {
      width: deep-get($config, 'sizes.sidebar');
      left: -(deep-get($config, 'sizes.sidebar'));
    }

    .-no-script & {
      overflow: auto;
      > .content {
        width: auto;
        position: static;
      }
    }
  }
}

