@import "../../../utilities/accessibility/a11y";

.menu-item {
  display: block;
  > .link {
    display: block;
    width: 100%;
    height: 100%;
  }
  > .link > .caption {
    display: block;
  }

  .-toc & > .link {
    display: inline-block;
    width: auto;
  }
  &.-image {
    > .link {
      position: relative;
      > .image {
        @extend ._sr-only;
      }
      > .display {
        display: block;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        > .image {
          background-size: cover;
          background-position: center;
          background-repeat: no-repeat;
          display: inline-block;
          vertical-align: top;
          opacity: 0.25;
        }
      }
      > .caption {
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
      }
    }
    &.-single > .link > .display > .image {
      width: 100%;
      height: 100%;
    }
    &.-double {
      &.-double-a > .link > .display > .image {
        width: 50%;
        height: 100%;
      }
      &.-double-b > .link > .display > .image {
        width: 100%;
        height: 50%;
      }
    }
    &.-triple {
      > .link > .display > .image {
        width: 50%;
        height: 50%;
      }
      &.-triple-a > .link > .display > .image:first-child {
        width: 50%;
        height: 100%;
        float: left;
      }
      &.-triple-b > .link > .display > .image:first-child {
        width: 100%;
        height: 50%;
      }
      &.-triple-c > .link > .display > .image:first-child + .image {
        width: 50%;
        height: 100%;
        float: right;
      }
      &.-triple-d > .link > .display > .image:last-child {
        width: 100%;
        height: 50%;
      }
    }
    &.-quad > .link > .display > .image {
      width: 50%;
      height: 50%;
    }
  }
}
