@import "../../../utilities/accessibility/a11y";

.player-seek {
  > .submit {
    display: none;
  }

  > .elapsed {
    float: left;
  }

  > .length {
    float: right;
  }

  > .display {
    @extend ._sr-only;
  }

  > .seek {
    clear: both;
  }

  .-no-script & {
    > .submit {
      display: inline-block;
    }

    > .display {
      @extend ._not-sr-only;
    }
  }
}

