@font-face {
  font-family: 'Mononoki';
  src: url('fonts/mononoki/mononoki-Regular.ttf') format('truetype'),
    url('fonts/mononoki/mononoki-Regular.woff') format('woff'),
    url('fonts/mononoki/mononoki-Regular.woff2') format('woff2'),
    url('fonts/mononoki/mononoki-Regular.eot') format('embedded-opentype');
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: 'Mononoki';
  src: url('fonts/mononoki/mononoki-Italic.ttf') format('truetype'),
  url('fonts/mononoki/mononoki-Italic.woff') format('woff'),
  url('fonts/mononoki/mononoki-Italic.woff2') format('woff2'),
  url('fonts/mononoki/mononoki-Italic.eot') format('embedded-opentype');
  font-weight: 400;
  font-style: italic;
}

@font-face {
  font-family: 'Mononoki';
  src: url('fonts/mononoki/mononoki-Bold.ttf') format('truetype'),
  url('fonts/mononoki/mononoki-Bold.woff') format('woff'),
  url('fonts/mononoki/mononoki-Bold.woff2') format('woff2'),
  url('fonts/mononoki/mononoki-Bold.eot') format('embedded-opentype');
  font-weight: 700;
  font-style: normal;
}

@font-face {
  font-family: 'Mononoki';
  src: url('fonts/mononoki/mononoki-BoldItalic.ttf') format('truetype'),
  url('fonts/mononoki/mononoki-BoldItalic.woff') format('woff'),
  url('fonts/mononoki/mononoki-BoldItalic.woff2') format('woff2'),
  url('fonts/mononoki/mononoki-BoldItalic.eot') format('embedded-opentype');
  font-weight: 700;
  font-style: italic;
}
