@import "../../../utilities/accessibility/scrollable";

.form-control {
  border: none;
  color: inherit;
  font-family: inherit;
  font-weight: inherit;
  line-height: inherit;
}

textarea.form-control {
  resize: vertical;
  @extend ._scrollable;
}

textarea.form-control.-no-resize {
  resize: none;
}

select.form-control[multiple] {
  @extend ._scrollable;
}
